<template>
  <div id="app">
    <div id="wrapper">
      <Search
        v-on:get-weather-current="getWeatherCurrent"
        v-on:get-forecast="getWeatherForecast"
        v-on:get-uv-index="getUVIndex"
        v-on:show-weather-current="showWeatherCurrent"
        v-on:show-weather-forecast="showWeatherForecast"
        v-on:show-uv-index="showUVIndex"
      ></Search>
      <Current v-if="show.weatherCurrent" v-bind:weatherDataCurrent="weatherDataCurrent"></Current>
      <Forecast v-if="show.weatherForecast" v-bind:weatherDataForecast="weatherDataForecast"></Forecast>
      <UV v-if="show.uvIndex" v-bind:uvData="uvData"></UV>
    </div>
  </div>
</template>

<script>
import axios from "axios";

import Search from "./components/Search";
import Current from "./components/Current";
import Forecast from "./components/Forecast";
import UV from "./components/UV";

export default {
  name: "App",
  components: {
    Search,
    Current,
    Forecast,
    UV
  },

  data() {
    return {
      cityName: "",
      
      exampleCitys:["Berlin","New York","Paris","Prag","Moskau","Male","Bangkok","Tokyo","Hawaii","Los Angeles","Kapstadt","Stockholm","Zurich","Washington DC"],

      weatherDataCurrent: [],
      weatherDataForecast: [],
      uvData: [],

      longitude: 0,
      latitude: 0,

      apiKey: "5c70cb6c1bf42d9dadd493558eb7ba68",
      units: "metric",

      show: {
        weatherCurrent: false,
        weatherForecast: false,
        uvIndex: false
      }
    };
  },
  mounted(){
    this.getWeatherCurrent(this.exampleCitys[Math.floor(Math.random() * Math.floor(14))]);
    this.show.weatherCurrent = true;
  },
  methods: {
    getWeatherCurrent(cityName) {
      axios
        .get(
          "http://api.openweathermap.org/data/2.5/weather?q=" +
            cityName +
            "&appid=" +
            this.apiKey +
            "&units=" +
            this.units +
            "&lang=de"
        )
        .then(res => (this.weatherDataCurrent = res.data))
        .catch(err => console.log(err));
    },

    getWeatherForecast(cityName) {
      axios
        .get(
          "http://api.openweathermap.org/data/2.5/forecast?q=" +
            cityName +
            "&appid=" +
            this.apiKey +
            "&units=" +
            this.units
        )
        .then(res => (this.weatherDataForecast = res.data))
        .catch(err => console.log(err));
    },

    getUVIndex() {
      this.setCoordinates();

      axios
        .get(
          "http://api.openweathermap.org/data/2.5/uvi?appid=" +
            this.apiKey +
            "&lat=" +
            this.latitude +
            "&lon=" +
            this.longitude
        )
        .then(res => (this.uvData = res.data))
        .catch(err => console.log(err));
    },
    setCoordinates() {
      this.longitude = this.weatherDataCurrent.coord.lon;
      this.latitude = this.weatherDataCurrent.coord.lat;
    },
    showWeatherCurrent() {
      this.show.weatherCurrent = true;
      this.show.weatherForecast = false;
      this.show.uvIndex = false;
    },
    showWeatherForecast() {
      this.show.weatherCurrent = false;
      this.show.weatherForecast = true;
      this.show.uvIndex = false;
    },
    showUVIndex() {
      this.show.weatherCurrent = false;
      this.show.weatherForecast = false;
      this.show.uvIndex = true;
    }
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
}
</style>
