<template>
  <div class="menu-1">
    <div class="flex-container-1">
      <div>
        <img src="../assets/himmel.png" ref="temp" width="35px" height="35px" />
      </div>
      <div>
        <label>Alles rund ums Wetter</label>
      </div>
      <div>
        <input
          type="text"
          v-model="cityName"
          name="newCityName"
          id="newCityName"
          placeholder="Ort"
          @keydown.enter="getWeatherCurrent(); showWeatherCurrent();"
        />
      </div>
    </div>
    <div class="flex-container-2">
      <div>
        <button type="submit" name="buttonCurrent" @mousedown="getWeatherCurrent(); showWeatherCurrent();">Wetter Aktuell</button>
      </div>
      <div>
        <button type="submit" name="buttonForecast" @mousedown="getWeatherForecast(); showWeatherForecast();">Wetter Vorhersage</button>
      </div>
      <div>
        <button type="submit" name="buttonUVIndex" @mousedown="getWeatherCurrent(); getUVIndex(); showUVIndex();">UV-Index</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      cityName: ""
    };
  },
  methods: {
    getWeatherCurrent() {
      if(this.cityName != "") {
        this.$emit("get-weather-current", this.cityName);
      }
    },
    getWeatherForecast() {
      if(this.cityName != "") {
        this.$emit("get-forecast", this.cityName);
      }
    },
    getUVIndex() {
      if(this.cityName != "") {
        this.$emit("get-uv-index");
      }
    },
    showWeatherCurrent() {
      if(this.cityName != "") {
      this.$emit("show-weather-current");
      }
    },
    showWeatherForecast() {
      if(this.cityName != "") {
      this.$emit("show-weather-forecast");
      }
    },
    showUVIndex() {
      if(this.cityName != "") {
      this.$emit("show-uv-index");
      }
    },
  }
};
</script>

